.general {
  font-family: "font-1", sans-serif;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.navigation{
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
    font-size: 1.3rem;
    /* align-content: flex-start; */
    width: 100%;
}
h3{
  margin-top: 20px;
  font-size: 1.4rem;
  font-weight: normal;
}

form {
  border: solid 1px grey;
  display: grid;
  padding: 14px;
  width: 510px;
  border-radius: 30px;
  background-color: hsl(221deg 2% 87%);
  margin-top: 25px;
  margin-bottom: 10px;
  justify-items: end;
  justify-content: center;
  box-shadow: 10px 5px 5px rgb(165, 164, 164, 0.6);
}
label {
  
  align-items: left;
}
#lab-contact{
  color:black;
}
input {
  margin: 10px;
  width: 220px;
  font-size: 1rem;
}

.message {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
textarea {
  width: 400px;
  height: 80px;
  margin-bottom: 10px;
}
button{
  padding-top: 5px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 5px;
  font-size: 1.1rem;
  box-shadow: 10px 5px 5px rgba(165, 164, 164, 0.7);
}

.modal {
padding-top: 15px;
padding-left: 50px;
    border-radius: 20px;
    width: 364px;
    font-size: 1.3rem;
    background-color: rgb(218, 223, 234);
    position: absolute;
    top: 14%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 49px;
    box-shadow: 10px 5px 5px rgba(165, 164, 164, 0.7);
}


