.countries {
  text-align: center;
  background-color:rgb(146, 144, 144);

  h2{
    padding:10px;
    color: rgb(208, 221, 223);
  }
  .radio-container {
    display: flex;
    align-items: center;
    margin-bottom: 21px;
    background: #53544d;
    border-radius: 20px;
    padding: 5px;
    color: rgb(243, 243, 243);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.452);
    flex-direction: row;
    flex-wrap: nowrap;
    input {
      transform: translateY(1px);
      cursor: pointer;
    }

    label {
      margin-left: 1px;
      cursor: pointer;
    }
  }
  input[type="text"] {
    padding: 10px;
    border-radius: 40px;
    font-size: 1.3rem;
    border: 3px solid $color-1;
    outline: none;
    transition: 0.4s;
    text-align: center;
    width: 200px;
    margin-bottom: 20px;

    &:focus {
      background: $color-1;
      border-radius: 4px;
      color: $white;
    }
  }

  button {
    margin: 0 auto 20px;
    background: none;
    border: 2px solid $color-1;
    padding: 4px 10px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.2s;
background: wheat;
    &:hover {
      background: $color-1;
      color :wheat
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
